@use "../../../styles/" as *;
.footer {
    background-color: #fff;
    padding: 15px 15px 0px !important;
    display: flex;
    @include flex(unset, center);
    flex-direction: column;
    box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.1), 0 2px 6px 2px rgba(60, 64, 67, 0.15);

    &__container {
        width: 1200px;
        @include responsive(lg) {
            width: 100%;
            padding: 0 12px;
        }
        @include responsive(tablet) {
            width: 100%;
            padding: 0 12px;
        }
        @include responsive(mobie) {
            width: 100%;
            padding: 0 12px;
        }
    }
    .title {
        color: $title-color;
        font-size: 16px;
        margin-bottom: 12px;
        &__list {
            margin-left: 8px;
            margin-top: 12px;
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            color: $p-color;
            font-size: 12px;
            width: 80%;
            li {
                a {
                    color: $p-color;
                    font-size: 12px;
                    display: block;
                    margin-right: 5px;
                    margin-bottom: 5px;
                    width: 48px;
                    height: 29px;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
    }
    &__bottom {
        margin-top: 15px;
        background: #f8f8f8;
        padding: 15px;
        box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.1), 0 2px 6px 2px rgba(60, 64, 67, 0.15);
        width: 100vw;
        @include flex(unset, center);
        flex-direction: column;
        background: #f8f8f8;
        min-height: 100px;
        padding: 15px;
        &__box {
            align-items: center;
            font-size: 10px;
            color: $p-color;
            &__logo {
                margin-top: 10px;
                @include flex(center, center);
            }
            &__dmca {
                margin-left: 10px;
                display: inline-block;
                width: 80px;
                height: 30px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
            &__sale {
                display: inline-block;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
    }
    &__container {
        @include responsive(lg) {
            width: 100%;
            padding: 0 12px;
        }
        @include responsive(tablet) {
            width: 100%;
            padding: 0 12px;
        }
        @include responsive(mobie) {
            width: 100%;
            padding: 0 12px;
        }
        &__text {
            font-size: 10px;
            color: $p-color;
            margin-bottom: 10px;
            font-weight: 500;
        }
    }
}
.box__payment {
    margin-top: 12px;
}
.info {
    ul {
        list-style: none;
        font-size: 12px;
        margin-left: 10px;
        li {
            margin-bottom: 5px;
            a {
                color: $p-color;
            }
        }
    }
}
.connect {
    &__social {
        display: flex;
        a {
            width: 44px;
            height: 32px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    &__title {
        color: $title-color;
        font-size: 16px;
        margin-top: 12px;
        margin-bottom: 12px;
    }
    &__support {
        p {
            font-size: 12px;
            color: $p-color;
            margin-bottom: 5px;
        }
    }
}
