.content__feature{background:#fff;border-radius:10px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);display:block;margin-bottom:15px;margin-right:auto;max-height:500px;min-height:0;overflow:hidden;position:relative;text-align:justify;width:100%;padding:15px}.content__feature__header{padding:8px;background-color:#f2f2f2;border-radius:.5rem;margin:auto;width:100%}.content__feature__header h2{color:#d70018;font-size:18px;font-weight:700;text-align:center}.content__feature__header ul{list-style:none}.content__feature__header ul li{display:flex;justify-content:unset;align-items:center}.content__feature__header ul li:before{align-items:center;color:#000;content:"";display:flex;font-size:20px;height:22px;margin-right:5px;width:auto}.content__feature__body{margin:10px 0}.content__feature__body p{font-size:14px;line-height:1.5;font-weight:500}.content__feature__body p strong{color:#363636;font-weight:700}.content__feature__body__btn{background:linear-gradient(180deg, hsla(0, 0%, 100%, 0), hsla(0, 0%, 100%, 0.91) 50%, #fff 55%);border-radius:10px;bottom:0;display:block;left:0;margin-bottom:0;padding-top:50px;position:absolute;right:0;text-align:center;width:100%}.content__feature__body__btn button{display:flex;justify-content:unset;align-items:center;background:#fff;border:none;border-radius:0 0 10px 10px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);color:#212529;cursor:pointer;display:flex;font-size:14px;height:34px;justify-content:center;margin-bottom:15px;margin-left:auto;margin-right:auto;max-width:335px;-webkit-text-decoration:none;text-decoration:none;width:100%}.content__feature__body__btn button:hover{background:#fef2f2;border:1px solid #d70018;color:#d70018}.content__feature__body__btn__icon{margin-left:16px;font-size:10px}.content__question{background-color:#f9fafb;border-radius:10px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);margin:15px auto;padding:10px}.content__question__title{color:#d02c35;font-size:24px;font-weight:700;margin:0;text-align:center;text-transform:uppercase}.content__param{border-radius:10px;box-shadow:0 1px 2px 0 rgba(60,64,67,.1),0 2px 6px 2px rgba(60,64,67,.15);max-width:370px;padding:12px 12px 4px}.content__param h2{color:#363636;font-size:16px;font-weight:600;line-height:1.125;margin-bottom:10px}