@use "../../../styles/" as *;

.header {
    @include flex(unset, center);
    height: 100%;
    width: 1200px;
    margin: 0 auto;
    @include responsive(tabletMobie){
        width: 100%;
    }
    &__users{
        cursor: pointer;
        margin-left: 10px;
        background: hsla(0,0%,100%,.2);
         @include flex(unset, center);
        flex-direction: column;
        color: #fff;
        min-width: 70px;
        border-radius: 8px;
        padding: 2px;
        height: 40px;
        &__icon{
             
                fill: none;
                stroke: #fff;
                stroke-width: 30px;
          
        }
        &__title{
            line-height: 1.2;
            font-size: 12px;
        }
    }
    &__service{
        @include responsive(tabletMobie){
            background: hsla(0,0%,100%,.2);
         
          margin: 0 10px;
          
        }
        display: flex;
        cursor: pointer;
        align-items: center;
        color: #fff;
        margin-left: 10px;
        height: 42px;
        padding: 3px 5px;
        border-radius: 10px;
        &:hover{
            background: hsla(0,0%,100%,.2);
        }
        &__icon{
            display: flex;
            align-items: center;
        }
        &__title{
            line-height: 0.6;
            & p{
                display: flex;
                flex-direction: column;
                 font-size: 12px;
                margin-left: 2px;
            }
        }
    }
    &__search {
      
        position: relative;
        width: 300px;
        display: flex;
        border-radius: 10px;
        overflow: hidden;
        align-items: center;
     @include responsive(tablet){
        width: 415px;
     }
     @include responsive(mobie){
        width: 170px;
     }
        &__icon {
            cursor: pointer;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 10px;
            font-weight: bold;
            font-size: 20px;
            color: $text-color;
        }
        &__input {
            width: 100%;
            height: 34px;
            border: none;
            outline: none;
            padding: 10px;
            font-size: 16px;
            color: #707070;
            @include responsive(tablet){
                font-size: 18px;
            }
        }
    }
    &__icon {
        fill: none;
        stroke: #fff;
        stroke-linecap: round;
        stroke-linejoin: round;
        font-weight: bold;
        &__wrapper {
            line-height: 64px;
            background: hsla(0, 0%, 100%, 0.2);
            align-items: center;
            border-radius: 10px;
            cursor: pointer;
            display: flex;
            height: 42px;
            padding: 7px 9px;
            position: relative;
            @include responsive(tablet){
                width: 130px;
                margin-left: 20px;
            
               
            }
            @include responsive(mobie){
                width: 120px;
           
            
               
            }
            & > p{
                color: #fff;
             
                font-size: 12px;
                margin-left: 4px;
                display: block;
            }
           
        }
        &__text{
            color: #fff;
       
            font-weight: 400;
            margin-left: 4px;
            line-height: 1.2;
            
            &__title{
                line-height: 17.1px;
                font-weight: 400;
                font-size: 12px;
                margin-left: 0;
            }
            &__adress{
                font-size: 13px;
                font-weight:500;

                
            }
        }
        &__wrapper  {
            margin-right: 10px;
            @include responsive(tablet){
                margin-right: 20px;     
            }
            @include responsive(mobie){
               margin: 0 5px;   
            }
        }
    }
    &__right {
        @include flex(unset,center);
        // width: 100%;

        &__icon{
            position: relative;
            @include flex(unset,unset);
            span{
                cursor: pointer;
                position: absolute;
                top: -17px;
                left: 9px;
                color: #fff;
                font-size: 12px;
                font-weight: 500;

        
    
            }
            &__wrapper{
                @include flex(center,center);  
                padding: 3px 5px;
                height: 43px;
                
                border-radius: 10px;
                @include responsive(tabletMobie){
                    background: hsla(0,0%,100%,.2);
                     @include flex(center,center);
                    width: 112px;

                    flex-direction: column;

                }
                @include responsive(mobie){
                    width: 46px;

                }
                &:hover{
                    background: hsla(0,0%,100%,.2);
                }
                &__text{
                    line-height: 1.3;
                    color: #fff;
                    font-weight: 400;
                    font-size: 11px;
                   @include responsive(tabletMobie){
                         @include flex(center,unset);
                    
                   }
                   p{
                    @include responsive(tabletMobie){
                        margin-left: 2px;
                   
                  }
                   }

                }
            }
        }
        &__avatar{
            margin-right: 10px;
        }
        &__user{
            @include flex(center,center);
            flex-direction: column;
            padding: 3px 5px;
         
            border-radius: 10px;
            margin-left: 6px;
            &:hover{
                background: hsla(0,0%,100%,.2);

            }
           line-height: 1;
            p {
                color: #fff;            
                font-size: 12px;
                font-weight: 600;
                line-height: 1.1;

            }
        }
        &__avatar {
            margin-left: 10px;
        }
    }
    &__content__popover {
        // padding-left: 15px;
        p {
            color: #707070;
            font-size: 15px;
            font-weight: 400;
            color: rgba(0, 0, 0, 0.87);
            padding: 4px 0;
            cursor: pointer;
        }
        &:hover {
            color: #00bfa5;
        }
    }
}

.nav {
    @include flex(unset, center);
    &__item {
        color: #fff;
        padding: 12px;
        font-size: 15px;
        font-weight: 500;
    }
    &__logo {
        @include flex(unset, center);
        padding: 0 12px;
        span {
            color: #fff;
            font-size: 24px;
            // font-weight: 700;
        }
        &__img{
            border: 1px solid #fff;
            padding: 2px;
            border-radius: 10px;
            width: 35px !important;
            height: 35px !important;
            object-fit: cover;
           
            content: url('data:image/png;base64,');
        }
        img {
            width: 160px;
            height: 30px;
            object-fit: cover;
        }
    }
}
.icon-cart {
    // font-size: 30px;
    color: #fff;
}
