@use "../../styles/" as *;
.card {
    border-radius: 15px;
    position: relative;
    box-shadow: 0 1px 2px 0 rgb(60 64 67 / 10%), 0 2px 6px 2px rgb(60 64 67 / 15%);
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    align-items: center;

    & .ant-card-body {
        padding: 0;
    }
    padding: 10px 8px 2px;
    z-index: 1;
}
.ant-card-cover {
    width: 160px;
    height: 160px;
    margin-bottom: 10px;
}
.ant-card-cover img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.ant-card-meta-title {
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    display: -webkit-box;
    font-size: 14px;
    font-weight: 600;
    height: 57px;
    overflow: hidden;
    position: relative;
    z-index: 10;
    text-align: left;
}
.ant-card-body {
    width: 100%;
}
.box-price {
    color: #444;
    display: flex;
    font-size: 15px;
    font-family: sans-serif;
    font-weight: 700;
    align-items: center;
    height: 100%;
    .box-price__new {
        color: #d70018;
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 10px;
        @include responsive(mobie) {
            font-size: 12px;
        }
    }
    .box-price__old {
        color: #707070;

        font-size: 14px;
        font-weight: 600;
        margin: 0 0 10px 10px;
        position: relative;
        -webkit-text-decoration: line-through;
        text-decoration: line-through;
        top: 2px;
        @include responsive(mobie) {
            font-size: 12px;
        }
    }
}
.description {
    align-items: flex-start;
    background: #f3f4f6;
    border: 1px solid #e5e7eb;
    border-radius: 5px;
    display: flex;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    --webkit-box-orient: vertical;
    overflow: hidden;
    max-height: 40px;
    line-height: 1.6;
    font-size: 12px;

    margin-left: 0;
    overflow: hidden;
    min-height: 50px;
    padding: 5px;
    text-transform: none;
    width: auto;
}
.product__price--percent {
    position: absolute;
    width: 80px;
    height: 31px;
    top: 0;
    left: -5px;
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 0 256 100"><title>Asset 1</title><g id="Layer_2" data-name="Layer 2"><g id="Layer_1-2" data-name="Layer 1"><image width="256" height="100" xlink:href="data:image/png;base64,"/></g></g></svg>')
        50% no-repeat;

    &-detail {
        color: #fff;
        font-size: 12px;
        font-weight: 700;
        margin: 5px 0 0;
        text-align: center;
    }
}

.rate {
    margin-top: 5px;
    text-align: left;
}
.ant-rate {
    font-size: 16px;
}
.btn-card-add {
    margin-top: 10px;
    width: 100%;
}
.btn-wish-list {
    @include flex(end, center);
    margin-bottom: 10px;
    &.active {
        .icon-active {
            display: inline-block;
            fill: #d70018 !important;
            color: #d70018;
        }
        .icon {
            display: none;
        }
    }

    text-align: right;
    span {
        // @include flex(center,unset);
        margin-right: 5px;
        color: #777;
        font-size: 12px;
    }
    .icon-active {
        display: none;
        fill: #d70018;
        color: #d70018;
    }
    .icon {
        color: #d70018;
        display: inline-block;
        font-size: 18px !important;
    }
}
